<script>
import HeaderComponent from "@/components/Header2/header2.vue";
import FooterComponent from "@/components/Footer/footer.vue";
import configSetting from "../../../../public/config/customSetting.json";
import newAxios from "axios";

export default {
  name: "index-component",
  components: { HeaderComponent, FooterComponent },
  data() {
    return {
      isShowPwd: false,
      isHideText: false,
      activeTab: "staff",
      form: {
        staffAccount: "",
        staffPwd: "",
        staffOtpCode: "",
        companyCompilation: "",
        companyEmail: "",
        companyPwd: "",
        companyOtpCode: "",
      },
      rules: {
        staffAccount: [{ required: true, message: "必填", trigger: "blur" }],
        staffPwd: [{ required: true, message: "必填", trigger: "blur" }],
        staffOtpCode: [{ required: true, message: "必填", trigger: "blur" }],
        companyCompilation: [
          { required: true, message: "必填", trigger: "blur" },
        ],
        companyEmail: [{ required: true, message: "必填", trigger: "blur" }],
        companyPwd: [{ required: true, message: "必填", trigger: "blur" }],
        companyOtpCode: [{ required: true, message: "必填", trigger: "blur" }],
      },
      footerHeight: 0,
    };
  },
  computed: {
    carouselImgs() {
      return this.$store.state.carouselImgs;
    },
    latestNews() {
      return this.$store.state.latestNews;
    },
  },
  methods: {
    submitForm(formName) {
      localStorage.clear();
      this.tabClickHandler();
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.activeTab === "staff") {
            this.getToken(
              this.form.staffOtpCode,
              this.form.staffAccount,
              this.form.staffPwd
            );
          } else {
            const account = `${this.form.companyCompilation};${this.form.companyEmail}`;
            this.getToken(
              this.form.companyOtpCode,
              account,
              this.form.companyPwd
            );
          }
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
          return false;
        }
      });
    },
    getToken(otpCode, account, pwd) {
      let apim = {};

      let params = {
        account: account.toLowerCase(),
        password: pwd,
        app_id: configSetting.APP_ID,
      };

      apim.APIM_GATEWAY = configSetting.APIM_GATEWAY;

      this.axios({
        url: configSetting.APIM_GATEWAY + configSetting.TWOFALOGIN_METHOD,
        method: "POST",
        data: params,
        // headers: {
        //   "content-type": "application/json",
        // },
      })
        .then((d) => {
          if (!d.data.data) return;
          let tempToken = d.data.data.access_token;

          // console.log(tempToken);

          //第二階段 2FAVerify
          var params2 = {
            app_id: configSetting.APP_ID,
            otp_code: otpCode,
          };

          this.getVerify(tempToken, params2, account, pwd);
        })
        .catch(() => {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "帳號密碼輸入錯誤",
          });
        });
    },
    getVerify(tempToken, data) {
      const customAxios = newAxios.create();
      customAxios({
        url: configSetting.APIM_GATEWAY + configSetting.TWOFAVERIFY_METHOD,
        method: "POST",
        headers: {
          Authorization: `Bearer ${tempToken}`,
          "content-type": "application/json",
        },
        data: data,
      })
        .then(async (res) => {
          if (!res.data.data) return;
          let data = res.data.data;
          if (data.refresh_token) {
            localStorage.setItem("af", data.refresh_token);
          }

          if (data.access_token) {
            this.getValidate();
          }
          return res;
        })
        .catch(() => {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: `失敗: 2FA Code 驗證失敗`,
          });
          // alert(err.message);
        });
    },
    // 登入成功後第一次打的API(後端需求)
    async getValidate() {
      try {
        await this.$store.dispatch("getValidate");
        this.$message({
          type: "success",
          duration: 5000,
          showClose: true,
          message: "登入成功",
        });
        this.$router.push({ name: "frontPage" });
      } catch (err) {
        console.log(err);
      }
    },
    // 切換角色判斷表單驗證
    tabClickHandler() {
      this.$refs.form.clearValidate();
      if (this.activeTab === "staff") {
        this.rules.staffAccount = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        // { min: 8, message: "至少8碼", trigger: "blur" },
        // { validator: this.validateRule, trigger: "blur" },
        this.rules.staffPwd = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.rules.staffOtpCode = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.rules.companyCompilation = [];
        this.rules.companyEmail = [];
        this.rules.companyPwd = [];
        this.rules.companyOtpCode = [];
      } else {
        this.rules.companyCompilation = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.rules.companyEmail = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.rules.companyPwd = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.rules.companyOtpCode = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.rules.staffAccount = [];
        this.rules.staffPwd = [];
        this.rules.staffOtpCode = [];
      }
    },
    // 目前沒用到
    validateRule(rule, value, callback) {
      const reg = /^(?=.*\d)(?=.*[a-zA-Z])(?=.*[~!@#$%^&*])[\da-zA-Z~!@#$%^&*]/;
      if (!reg.test(value)) {
        callback(new Error("必須包含數字/符號/英文大小寫混合"));
      } else {
        callback();
      }
    },
    getInitTokenAsync() {
      return new Promise((resolve) => {
        let p4use = {};
        let acnt = "account";
        let v1 = "12800225;evendor_support@innolux.com";
        let pswd = "password";
        let v2 = "1QaX2WsX";
        let apid = "app_id";
        let v3 = configSetting.APP_ID;

        p4use[acnt] = v1;
        p4use[pswd] = v2;
        p4use[apid] = v3;
        let params = p4use;
        this.axios({
          url: configSetting.APIM_GATEWAY + configSetting.TWOFALOGIN_METHOD,
          method: "POST",
          data: params,
          headers: {
            "content-type": "application/json",
          },
        }).then(() => {
          resolve();
        });
      });
    },
    getInitToken() {
      let p4use = {};
      let acnt = "account";
      let v1 = "12800225;evendor_support@innolux.com";
      let pswd = "password";
      let v2 = "1QaX2WsX";
      let apid = "app_id";
      let v3 = configSetting.APP_ID;

      p4use[acnt] = v1;
      p4use[pswd] = v2;
      p4use[apid] = v3;
      let params = p4use;
      this.axios({
        url: configSetting.APIM_GATEWAY + configSetting.TWOFALOGIN_METHOD,
        method: "POST",
        data: params,
        headers: {
          "content-type": "application/json",
        },
      }).then(() => {
        this.getIndexInfo();
      });
    },
    // 目前沒用到
    chkReg(t) {
      const reg = /[a-zA-Z]+/;
      if (reg.test(t)) {
        return t;
      } else {
        throw "Error";
      }
    },
    async downloadPdf() {
      const file = encodeURI("2FA認證機制操作及說明文件.pdf");
      await this.getInitTokenAsync();
      const res = await this.$store.dispatch("getDownloadPdf", {
        file_id: file,
        router_Name: configSetting.getLogin,
      });
      this.downloadFile(res, "2FA認證機制操作及說明文件", "pdf");
    },
    async getIndexInfo() {
      try {
        await this.$store.dispatch("getIndexInfo");
        localStorage.clear();
      } catch (err) {
        console.log(err);
      }
    },
  },
  async mounted() {
    // 進入index時都把localStorage清除
    localStorage.clear();

    this.getInitToken();

    this.$store.commit("HEADER_HANDLER", false);
    this.$store.commit("BREADCRUMB_HANDLER", false);
    this.$store.state.loginSidebar = [];
    this.$root.$children[0].sidebarSwitch = false;

    const footer = document.querySelector(".footer");
    if (footer) {
      this.footerHeight = footer.offsetHeight;
    }
  },
};
</script>

<template src="./index.html"></template>
<style lang="sass" scoped src="./index.sass"></style>
